import { createAction, props } from '@ngrx/store';
import { ExternalSqlSourceViewModel, CreateChartViewModel } from './create.models';
import { ChartDescription } from '../shared/shared.models';

export const LoadDataSource = createAction(
    '[Create] Load data source',
    props<{ chartId: string }>()
);

export const LoadDataSourceSuccess = createAction(
    '[Create] Load data source success',
    props<{ dataSource: any }>()
);

export const LoadDataSourceFailure = createAction(
    '[Create] Load data source failure'
);

export const SaveSqlDataSource = createAction(
    '[Create] Save SQL data source',
    props<{ chartId: string, dataSource: ExternalSqlSourceViewModel }>()
);

export const SaveSqlDataSourceSuccess = createAction(
    '[Create] Save SQL data source success',
    props<{ dataSource: ExternalSqlSourceViewModel }>()
);

export const SaveSqlDataSourceFailure = createAction(
    '[Create] Save SQL data source failure'
);

export const TestSqlConnection = createAction(
    '[Create] Test sql connection',
    props<{ dataSource: ExternalSqlSourceViewModel }>()
);

export const TestSqlConnectionSuccess = createAction(
    '[Create] Test sql connection success'
);

export const TestSqlConnectionFailure = createAction(
    '[Create] Test sql connection failure'
);

export const ResetSaveStatus = createAction(
    '[Create] Reset save status'
);

export const CreateChart = createAction(
    '[Create] Create chart',
    props<{ model: CreateChartViewModel }>()
);

export const CreateChartSuccess = createAction(
    '[Create] Create chart success',
    props<ChartDescription>()
);

export const CreateChartFailure = createAction(
    '[Create] Create chart failure'
);
