import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { L10N_LOCALE, L10nLocale } from 'angular-l10n';
import * as fromRoot from '../../reducers';
import * as Resources from '../../strings';
import * as fromCreate from '../create.reducers';
import { Store, select } from '@ngrx/store';
import { takeUntil, filter, withLatestFrom } from 'rxjs/operators';
import { LoadDataSource, TestSqlConnection, SaveSqlDataSource, ResetSaveStatus } from '../create.actions';
import { ExternalSqlSourceViewModel } from '../create.models';
import { Router } from '@angular/router';

@Component({
    selector: 'toc-sql-source',
    templateUrl: './sql-source.component.html',
    styleUrls: ['./sql-source.component.scss']
})
export class SqlSourceComponent implements OnInit, OnDestroy {
    Resources = Resources;
    chartId$: Observable<string>;
    chartId: string;
    dataSource$: Observable<ExternalSqlSourceViewModel>;
    hasConnectionTestSucceeded$: Observable<boolean>;
    hasConnectionTestFailed$: Observable<boolean>;
    isTestingConnection$: Observable<boolean>;
    model: ExternalSqlSourceViewModel = {
        ConnectionString: '',
        ParentColumn: '',
        PrimaryKeyColumn: '',
        SearchQuery: '',
        SelectQuery: ''
    };
    connectionFail = false;
    connectionSuccess = false;
    private unsubscribe: Subject<void> = new Subject();

    constructor(@Inject(L10N_LOCALE) public locale: L10nLocale, private store: Store<fromRoot.State>, private router: Router) {
        this.chartId$ = this.store.pipe(select(fromRoot.getChartId));
        this.chartId$.pipe(takeUntil(this.unsubscribe), filter(x => x !== '')).subscribe(x => {
            store.dispatch(LoadDataSource({ chartId: x }));
            this.chartId = x;
        });
        this.dataSource$ = this.store.pipe(select(fromCreate.selectDataSource));
        this.dataSource$.pipe(takeUntil(this.unsubscribe)).subscribe(x => {
            this.model = { ...x };
        });
        this.hasConnectionTestFailed$ = this.store.pipe(select(fromCreate.selectHasConnectionTestFailed));
        this.hasConnectionTestSucceeded$ = this.store.pipe(select(fromCreate.selectHasConnectionTestSucceeded));
        this.store.pipe(
            select(fromCreate.selectSaveSqlSourceSuccess),
            takeUntil(this.unsubscribe),
            filter(x => x),
            withLatestFrom(this.chartId$)
        ).subscribe(([_, chartId]) => {
            this.router.navigate(['/chart', chartId]);
            this.store.dispatch(ResetSaveStatus());
        });
    }

    ngOnInit() {
    }

    testConnection() {
        this.store.dispatch(TestSqlConnection({ dataSource: this.model }));
    }

    save() {
        this.store.dispatch(SaveSqlDataSource({ chartId: this.chartId, dataSource: this.model }));
    }

    ngOnDestroy(): void {
        this.unsubscribe.next();
        this.unsubscribe.complete();
    }
}
