import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CreateChartComponent } from './components/create-chart.component';
import { SqlSourceComponent } from './components/sql-source.component';

const routes: Routes = [
    { path: '', component: CreateChartComponent },
    { path: 'sql/:chartId', component: SqlSourceComponent }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class CreateRoutingModule { }
