import { createFeatureSelector, createSelector, createReducer, Action, on } from '@ngrx/store';

import * as fromRoot from '../reducers';
import { LoadDataSourceSuccess, TestSqlConnection, TestSqlConnectionSuccess, TestSqlConnectionFailure, SaveSqlDataSourceSuccess, ResetSaveStatus, CreateChartSuccess, CreateChart, CreateChartFailure } from './create.actions';
import { ExternalSqlSourceViewModel } from './create.models';
import { ChartDescription } from '../shared/shared.models';

export const featureKey = 'create';

export interface CreateState {
    dataSource: ExternalSqlSourceViewModel;
    isTestingConnection: boolean;
    hasConnectionTestFailed: boolean;
    hasConnectionTestSucceeded: boolean;
    saveSqlSourceSuccess: boolean;
    createdChart: ChartDescription;
    isCreating: boolean;
}

export interface State extends fromRoot.State {
    create: CreateState;
}

export const initialState: CreateState = {
    dataSource: null,
    isTestingConnection: false,
    hasConnectionTestFailed: false,
    hasConnectionTestSucceeded: false,
    saveSqlSourceSuccess: false,
    createdChart: null,
    isCreating: false
};

const createChartReducer = createReducer(initialState,
    on(LoadDataSourceSuccess, (state, action) => ({ ...state, dataSource: action.dataSource, hasConnectionTestFailed: false, hasConnectionTestSucceeded: false })),
    on(TestSqlConnection, (state) => ({ ...state, isTestingConnection: true })),
    on(TestSqlConnectionSuccess, (state) => ({ ...state, isTestingConnection: true, hasConnectionTestFailed: false, hasConnectionTestSucceeded: true })),
    on(TestSqlConnectionFailure, (state) => ({ ...state, isTestingConnection: true, hasConnectionTestFailed: true, hasConnectionTestSucceeded: false })),
    on(SaveSqlDataSourceSuccess, (state) => ({ ...state, saveSqlSourceSuccess: true })),
    on(CreateChart, (state) => ({ ...state, isCreating: true })),
    on(CreateChartSuccess, (state, action) => ({ ...state, createdChart: action })),
    on(CreateChartFailure, (state) => ({ ...state, isCreating: false })),
    on(ResetSaveStatus, (state) => ({ ...state, saveSqlSourceSuccess: false, createdChart: null, isCreating: false }))
);

export function reducer(state: CreateState | undefined, action: Action) {
    return createChartReducer(state, action);
}

export const selectCreate = createFeatureSelector<State, CreateState>(featureKey);

export const selectIsTestingConnection = createSelector(selectCreate, state => state.isTestingConnection);
export const selectDataSource = createSelector(selectCreate, state => state.dataSource);
export const selectHasConnectionTestFailed = createSelector(selectCreate, state => state.hasConnectionTestFailed);
export const selectHasConnectionTestSucceeded = createSelector(selectCreate, state => state.hasConnectionTestSucceeded);
export const selectSaveSqlSourceSuccess = createSelector(selectCreate, s => s.saveSqlSourceSuccess);
export const selectCreatedChart = createSelector(selectCreate, s => s.createdChart);
export const selectIsCreating = createSelector(selectCreate, s => s.isCreating);
