import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { CreateRoutingModule } from './create-routing.module';
import { CreateChartComponent } from './components/create-chart.component';
import { L10nTranslationModule } from 'angular-l10n';
import { FormsModule } from '@angular/forms';
import { StoreModule } from '@ngrx/store';
import * as fromCreate from './create.reducers';
import { SqlSourceComponent } from './components/sql-source.component';
import { EffectsModule } from '@ngrx/effects';
import { CreateEffects } from './create.effects';
import { CreateChartService } from './create-chart.service';

@NgModule({
    declarations: [CreateChartComponent, SqlSourceComponent],
    imports: [
        FormsModule,
        L10nTranslationModule,
        CommonModule,
        CreateRoutingModule,
        StoreModule.forFeature(fromCreate.featureKey, fromCreate.reducer),
        EffectsModule.forFeature([CreateEffects])
    ],
    providers: [
        CreateChartService
    ]
})
export class CreateModule { }
