import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ExternalSqlSourceViewModel, CreateChartViewModel } from './create.models';
import { ChartDescription } from '../shared/shared.models';

@Injectable()
export class CreateChartService {
    constructor(private http: HttpClient) {}

    createChart(model: CreateChartViewModel) {
        return this.http.post<ChartDescription>('/api/create/chart', model);
    }

    getDataSource(chartId: string) {
        return this.http.get<any>(`/api/create/sql/${chartId}`);
    }

    saveSqlDataSource(chartId: string, model: ExternalSqlSourceViewModel) {
        return this.http.post<ExternalSqlSourceViewModel>(`/api/create/sql/${chartId}`, model);
    }

    testSqlConnection(dataSource: ExternalSqlSourceViewModel) {
        return this.http.post<string>('/api/testconnection', dataSource);
    }
}
