import { Injectable } from '@angular/core';
import { Actions, Effect, ofType, createEffect } from '@ngrx/effects';
import { EMPTY, of } from 'rxjs';
import { map, mergeMap, catchError, exhaustMap, switchMap } from 'rxjs/operators';
import { CreateChartService } from './create-chart.service';
import { LoadDataSource, LoadDataSourceSuccess, LoadDataSourceFailure, TestSqlConnection, TestSqlConnectionSuccess, TestSqlConnectionFailure, SaveSqlDataSource, SaveSqlDataSourceSuccess, SaveSqlDataSourceFailure, CreateChart, CreateChartSuccess, CreateChartFailure } from './create.actions';
import { ToastSuccess, ToastError } from '../shared/shared.actions';

@Injectable()
export class CreateEffects {
    constructor(private actions$: Actions, private createChartService: CreateChartService) {}

    createChart$ = createEffect(() => this.actions$.pipe(
        ofType(CreateChart),
        switchMap(action => this.createChartService.createChart(action.model).pipe(map(data => CreateChartSuccess(data)))),
        catchError(error => of(CreateChartFailure()))
    ));

    loadDataSource$ = createEffect(() => this.actions$.pipe(
        ofType(LoadDataSource),
        switchMap(action => this.createChartService.getDataSource(action.chartId).pipe(map(data => LoadDataSourceSuccess({ dataSource: data })))),
        catchError(error => of(LoadDataSourceFailure()))
    ));

    saveSqlDataSource$ = createEffect(() => this.actions$.pipe(
        ofType(SaveSqlDataSource),
        switchMap(action => this.createChartService.saveSqlDataSource(action.chartId, action.dataSource).pipe(mergeMap(data =>
            [SaveSqlDataSourceSuccess({ dataSource: data }), new ToastSuccess('Data source saved.')]
        ))),
        catchError(error => of(SaveSqlDataSourceFailure()).pipe(mergeMap(x => [x, new ToastError('Could not save data source.')])))
    ));

    testSqlConnnection$ = createEffect(() => this.actions$.pipe(
        ofType(TestSqlConnection),
        switchMap(action => this.createChartService.testSqlConnection(action.dataSource).pipe(mergeMap(data => {
            if (data === 'OK') {
                return [TestSqlConnectionSuccess(), new ToastSuccess('Connection test succeeded.')];
            } else {
                return [TestSqlConnectionFailure(), new ToastError('Connection test failed.')];
            }
        }))),
        catchError(error => of(TestSqlConnectionFailure()).pipe(mergeMap(x => [x, new ToastError('Connection test failed on the server.')])))
    ));
}
