import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { Store, select } from '@ngrx/store';
import { Subject, combineLatest, Observable } from 'rxjs';
import * as fromRoot from '../../reducers';
import * as fromCreate from '../create.reducers';
import * as Resources from '../../strings';
import { DataSourceDescription, demoSource, manualSource, azureTableSource, excelSource, sqlSource, upsSourceDisabled, upsSource, spListSourceDisabled, spListSource, waadSource, waadSourceDisabled } from '../create.models';
import { PreloadedData } from 'src/app/models';
import { takeUntil, filter } from 'rxjs/operators';
import { CreateChart, ResetSaveStatus } from '../create.actions';
import { Router } from '@angular/router';
import { ToastError } from 'src/app/shared/shared.actions';
import { ChartDescription } from 'src/app/shared/shared.models';
import { InsertChartDescription } from 'src/app/actions';
import { AnalyticsService } from 'src/app/core/services/analytics.service';
import { L10nTranslationService, L10N_LOCALE, L10nLocale } from 'angular-l10n';

@Component({
    selector: 'toc-create-chart',
    templateUrl: './create-chart.component.html',
    styleUrls: ['./create-chart.component.scss']
})
export class CreateChartComponent implements OnInit, OnDestroy {
    Resources = Resources;
    dataSources: DataSourceDescription[] = [];
    dataSourcesHighlight: boolean[] = [];
    preloadedData: PreloadedData;
    createChartModel = {
        placeholder: '',
        isPublic: true,
        name: ''
    };
    placeholderName = 'Chart #{0}';
    isCreating$: Observable<boolean>;
    private unsubscribe: Subject<void> = new Subject();

    constructor(@Inject(L10N_LOCALE) public locale: L10nLocale, private store: Store<fromRoot.State>, private translation: L10nTranslationService, private router: Router, private analytics: AnalyticsService) {
        this.analytics.send('Create chart', {});
        combineLatest([this.translation.onChange(), this.store.select(fromRoot.getPreloadedData)])
            .pipe(takeUntil(this.unsubscribe))
            .subscribe(([_, data]) => {
                this.placeholderName = this.translation.translate(Resources.CreateChartStrings.TXT.ChartNamePlaceholder);
                this.preloadedData = data;
                const sources: DataSourceDescription[] = [sqlSource, excelSource, manualSource, azureTableSource, demoSource];
                if (this.preloadedData.isSharePointOnline) {
                    sources.unshift(spListSource);
                    sources.unshift(upsSource);
                } else {
                    sources.unshift(spListSourceDisabled);
                    sources.unshift(upsSourceDisabled);
                }
                if (this.preloadedData.isWAADTenant) {
                    sources.unshift(waadSource);
                } else {
                    sources.unshift(waadSourceDisabled);
                }
                this.dataSources = sources;
                this.dataSourcesHighlight = this.dataSources.map(x => false);

                for (let i = 1; i < 256; i++) {
                    const placeholderName = this.placeholderName.replace('{0}', String(i));
                    if (this.preloadedData.fullChartList.findIndex(chart => chart.name === placeholderName) === -1) {
                        this.createChartModel = {
                            ...this.createChartModel,
                            placeholder: placeholderName
                        };
                        break;
                    }
                }
            });
        this.store.pipe(takeUntil(this.unsubscribe), select(fromCreate.selectCreatedChart), filter(x => x !== null)).subscribe(chart => {
            //proceed further based on chart type
            this.store.dispatch(new InsertChartDescription(chart));
            switch (chart.dataSourceType) {
                case 13: { //JSON
                    this.router.navigateByUrl(`/chart/${chart.id}?EditMode=1`);
                    break;
                }
                case 2: { //SQL
                    this.router.navigate(['/create/sql', chart.id]);
                    break;
                }
                case 5: { //WAAD
                    window.location.href = `/PickTop/${chart.id}?create=create`;
                    break;
                }
                case 6: { //SP List
                    window.location.href = `/sharepointlist/${chart.id}`;
                    break;
                }
                case 7: { //UPS
                    window.location.href = `/PickTop/${chart.id}?create=create`;
                    break;
                }
                case 8: { //Demo
                    this.router.navigate(['/chart', chart.id]);
                    break;
                }
                case 12: { //Azure Table
                    window.location.href = `/CreateChart/AzureTable?OrgChartDefinitionId=${chart.id}`;
                    break;
                }
                default: {
                    this.store.dispatch(new ToastError('Unknown data source type'));
                }
            }
            this.store.dispatch(ResetSaveStatus());
        });
        this.isCreating$ = store.pipe(select(fromCreate.selectIsCreating));
    }

    ngOnInit() {
        console.log('on init');
        this.store.dispatch(ResetSaveStatus());
    }

    createChart(type: number) {
        this.store.dispatch(CreateChart({
            model: {
                ChartName: this.createChartModel.name || this.createChartModel.placeholder,
                IsPublic: this.createChartModel.isPublic,
                DataSourceType: type
            }
        }));
    }

    onMouseEnter($event: any, index: number) {
        this.dataSourcesHighlight = this.dataSources.map((value, idx) => idx !== index);
    }

    onMouseLeave() {
        this.dataSourcesHighlight = this.dataSources.map(x => false);
    }

    ngOnDestroy(): void {
        this.unsubscribe.next();
        this.unsubscribe.complete();
    }
}
